.TodoList {
    padding-top: 50px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.inputPanel, .sortPanel {
    margin-bottom: 20px;
}

.task-details {
    display: flex;
    flex-direction: column;
}

.task-actions {
    display: flex;
    gap: 10px;
}

.error {
    color: red;
    margin-top: 10px;
}
